<template>
<div>
    <v-row no-gutters justify="center">
    <v-col cols="10">
    <v-card flat height="500" color="rgba(0,0,0,0)">
        <v-list color="rgba(0,0,0,0)">
            <hr color="#487093" size="10px">
            <template>
                <v-simple-table>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-left">id</th>
                        <th class="text-left">title</th>
                        <!-- <th class="text-left">date</th> -->
                        <th class="text-left">hits</th>
                        <th class="text-left">writer</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="notice in notices"
                        :key="notice.id"
                      >
                        <td>{{ notice.id }}</td>
                        <td>{{ notice.title }}</td>
                        <!-- <td>{{ notice.date }}</td> -->
                        <td>{{ notice.hits }}</td>
                        <td>{{ notice.writer }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
            </template>
        </v-list>
        <v-card router :to="'write'">글쓰기</v-card>
    </v-card>
    </v-col>
    </v-row>
</div>
</template>
<script>

export default {
    created () {
        this.$http.get('/api/notices')
        .then((response) => {
        this.notices = response.data
        })
    },
    methods:{
        click:function(road_id){
            if(this.$route.params.road_id != road_id){
                this.$router.push({path:`//notice/${road_id}`})
            }
        },
        pageidx:function(){
            this.road_idx = this.$route.params.road_id-1 // mapping id(starting from 1) to index(starting from 0)
            return this.road_idx;
        }
    },
    
    data(){
        return{
            notices:[]
        }
    }
    
}
</script>